import React, {useState} from 'react';
import Slider from './Slider';
import {Link} from 'react-router-dom';
import { Button } from 'react-bootstrap';
import axios from 'axios';

export default function MainList(props) {

    let [aCount, setACount] = useState(0);
    let [buttonView, setButtonView] = useState(true);

    return(
        <>        
            <Slider/>
            <div className="container">
                <div className="row">
                    {
                        props.shoesData.map((x,i) => {
                            return (
                                <div className="col-md-4" key={i}>
                                    <Link to={`/detail/${x.id}`}>
                                        <img src={`https://codingapple1.github.io/shop/shoes${x.id*1+1}.jpg`} width="100%" alt={`shoes${x.id*1+1}`}/>
                                        <h4>{x.title}</h4>
                                        <p>{x.content}</p>
                                        <p>{x.price}원</p>
                                    </Link>
                                </div>
                            )
                        })
                    
                    }
                </div>
            </div>
            {
                buttonView === true
                ?
                <Button variant="primary" onClick={()=>{
                    if(aCount === 1){
                        setButtonView(false);
                    }
                    if(aCount >= 2){
                        return;
                    }else {
                        axios.get(`https://codingapple1.github.io/shop/data${aCount*1+2}.json`)
                        .then((result)=>{
                            props.setShoesData([...props.shoesData,...result.data]);
                            setACount(aCount+1);
                        })
                        .catch(()=>{
                            console.log('실패입니다');
                        })    
                    }
                }}>Primary</Button>            
                : null
            }
        </>
    )
}


