import React, {useEffect, useState} from 'react';
import {Card, Button} from 'react-bootstrap';
import {useNavigate, useParams} from 'react-router-dom';
import {Toast} from 'react-bootstrap'

export default function Detail(props){
    let navigate = useNavigate();
    let {id} = useParams();
    let [alert, setAlert] = useState(true);
    
    useEffect(()=>{
        let timer = setTimeout(()=>{
            setAlert(false);
        },2000);
        return ()=>{
            clearTimeout(timer);
        }
    },[]);

    return (
        <>
        {
            alert === true
            ? <Toast style={{width: '50%', margin:'30px auto 0 auto'}}>
                <Toast.Header>
                    <img src="holder.js/20x20?text=%20" className="rounded me-2" alt="" />
                    <strong className="me-auto">Bootstrap</strong>
                    <small>11 mins ago</small>
                </Toast.Header>
                <Toast.Body>주의하세요 재고가 얼마 남지 않았습니다</Toast.Body>
            </Toast>
            : null
        }


            
            <Card style={{ width: '50%', margin:'30px auto'}}>
                <Card.Img variant="top" src={`https://codingapple1.github.io/shop/shoes${id*1+1}.jpg`} />
                <Card.Body>
                    <Card.Title>{props.shoesData[id].title}</Card.Title>
                    <Card.Text>
                    {props.shoesData[id].content}
                    </Card.Text>
                    <Card.Text>
                    {props.shoesData[id].price}
                    </Card.Text>
                    <Button variant="primary" onClick={()=>{navigate(-1);}}>Go Back</Button>
                </Card.Body>
            </Card>
        </>
    );
}